.modal {
  display: none;
  height: 100vh;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  overflow: hidden;
}

.modal__bg {
  background: rgba(0, 0, 0, 0.8);
  height: 100vh;
  position: absolute;
  width: 100%;
  overflow: hidden;
}

.modal__content {
  width: 90%;
  max-width: 825px;
  height: 80%;
  overflow: scroll;
  background: #fff;
  padding: 3%;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.att_popup .modal__content {
  height: auto;
  padding: 1%;
  overflow: hidden;
  max-width: 490px;
}

.js-modal-close_btn {
  box-sizing: border-box;
  display: block;
  background: #444;
  height: 50px;
  width: 50px;
  line-height: 38px;
  border: 2px solid #fff;
  color: #fff;
  text-align: center;
  font-size: 4.5rem;
  border-radius: 100%;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translate(-50%, 50%);
}

@media screen and (max-width: 800px) {
  .modal__content {
    width: 90%;
    height: 80%;
  }
}
