  display: flex;
  width: 100%;
  height: 55px;
  justify-content: flex-end;
  align-items: center;
}

.site_header_login summary:after {
  content: "";
  display: block;
  width: 5px;
  height: 5px;
  margin: 5px 0 0 10px;
  border: 4px solid transparent;
  border-top: 5px solid #fff;
  box-sizing: border-box;
  transition: 0.1s;
