/* ====================================================
Utility
==================================================== */
@media screen and (min-width: 801px) {
	.smt {
		display: none !important;
	}
}
@media screen and (max-width: 800px) {
	html {
		font-size: 50%;
	}
	.pc {
		display: none !important
	}
}



// Hover style
.u-fn-hover{
	display: inline-block;
	@include mq(md-min){
		transition: all .2s;
		&:hover{
			opacity: .7;
		}
	}
}
