@charset "utf-8";
@import "vars";//setting style and Shortcut css writing
@import "module";
@import "component";//Common button, title, text setting(small component)
@import "utility";//PC, SP switching, Common Animation, hover action, change appearance by JS except for Header and Footer


html {
	width: 100%;
	font-size: 62.5%;
	scroll-behavior: smooth;
}
body {
	background-color: #fff;
	color: #464646;
	font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
	font-size: 1.4rem;
	line-height: 1.6;
	width: 100%;
	height: 100%;
	position: relative;
	-webkit-text-size-adjust: 100%;
}
a {
	text-decoration: underline;
	color: #464646;
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}
a, button {
	transition: all .6s ease 0s;
}
a:hover, button:hover {
	opacity: .7;
}
strong, em {
	font-style: normal;
	font-weight: 300
}
ul, li {
	list-style: none;
	word-wrap: break-word;
}
p {
	word-wrap: break-word;
}
#wrapper {
	position: relative;
	width: 100%
}
.inr {
	max-width: 1000px;
	margin: 0 auto;
	padding: 0 3%;
}
.mincho {
	font-family: "ヒラギノ明朝 Pro W4", "Hiragino Mincho Pro", "HGS明朝B", "MingLiU", "SimSun", "ＭＳ Ｐ明朝", serif !important;
	transform: rotate(.03deg);
}
.times {
	font-family: Times, "Times New Roman", serif !important;
}
.baloo {
	font-family: 'Baloo Paaji', cursive;
}
.maru {
	font-family: "ヒラギノ丸ゴ ProN W4", "ヒラギノ丸ゴ ProN", "Hiragino Maru Gothic ProN", "Hiragino Maru Gothic ProN", Meiryo, Arial, sans-serif;
}
.tx-red {
	color: #F04850 !important;
}
.tx-pink {
	color: #ED7A9B !important;
}
.tx-green {
	color: #30A72B !important;
}
.tx-blue {
	color: #5BC0DE !important;
}
.tx-small {
	font-size: 85% !important;
}
.tc {
	text-align: center !important;
}
.tr {
	text-align: right !important;
}
.tl {
	text-align: left !important;
}
.bold {
	font-weight: bold !important;
}
.bg_white {
	background-color: #fff !important;
}
.bg_blue {
	background-color: #D1EBF6 !important;
}
.bg_pink {
	background-color: #FDF1F5 !important;
}
.bg_gray {
	background-color: #F4F4F4 !important;
}
.bg_yellow {
	background-color: #FDF1CD !important;
}
.bg_blue .inr,
.bg_white .inr,
.bg_pink .inr,
.bg_gray .inr {
	padding: 3%;
}
.flex_box {
	display: flex;
}
.f-jc-space-between {
	justify-content: space-between !important;
}
.f-jc-space-around {
	justify-content: space-around !important;
}
.f-jc-flex-start {
	justify-content: flex-start !important;
}
.f-jc-flex-end {
	justify-content: flex-end !important;
}
.f-ai-center {
	align-items: center;
}
.f-ai-end{
	align-items: end !important;
}
.linethrough {
	text-decoration: line-through !important;
}
.underline{
	text-decoration: underline;
}
.w100p {
	width: 100%;
}
.cfx:after {
	content: ".";
	display: block;
	height: 0;
	font-size: 0;
	clear: both;
	visibility: hidden
}
.cfx {
	display: inline-block
}
* html .cfx {
	height: 1%
}
.cfx {
	display: block
}
img {
	max-width: 100%;
	width: 100%;
}

/* iPhone or Safari overrides */
button, input[type='submit'], input[type='reset'] {
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	appearance: none;
	border-radius: 0;
}

@media screen and (min-width: 801px) {
	.smt {
		display: none !important;
	}
}
@media screen and (max-width: 800px) {
	html {
		font-size: 50%;
	}
	.pc {
		display: none !important
	}
}

.ac_box_btn {
	cursor: pointer;
}
.__ac_box_cont {
	display: none;
}
.modal {
	display: none;
	height: 100vh;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 9999;
	overflow: hidden;
}
.modal__bg {
	background: rgba(0, 0, 0, 0.8);
	height: 100vh;
	position: absolute;
	width: 100%;
	overflow: hidden;
}
.modal__content {
	width: 90%;
	max-width: 825px;
	height: 80%;
	overflow: scroll;
	background: #fff;
	padding: 3%;
	position: absolute;
	top: 45%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.att_popup .modal__content {
	height: auto;
	padding: 1%;
	overflow: hidden;
	max-width: 490px;
}
.js-modal-close_btn {
	box-sizing: border-box;
	display: block;
	background: #444;
	height: 50px;
	width: 50px;
	line-height: 38px;
	border: 2px solid #fff;
	color: #fff;
	text-align: center;
	font-size: 4.5rem;
	border-radius: 100%;
	position: absolute;
	bottom: 5%;
	left: 50%;
	transform: translate(-50%, 50%);
}
@media screen and (max-width: 800px) {
	.modal__content {
		width: 90%;
		height: 80%;
	}
}
/* ======================================================
	Header
====================================================== */
.site_header {
	/*	position: fixed;
	top: 0;
	z-index: 999;*/
	width: 100%;
	background-color: #464646;
	color: #fff;
	line-height: 1;
}
.site_header_inr {
	width: 100%;
	height: 55px;
	box-sizing: border-box;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 20px;
}
.site_header a {
	display: block;
	width: 195px;
}
.site_header a img {
	width: 100%;
}
.site_header_login {
	width: 165px;
	position: relative;
}
.site_header_login summary {
	display: flex;
	width: 100%;
	height: 55px;
	justify-content: flex-end;
	align-items: center;
}
.site_header_login summary:after {
	content: "";
	display: block;
	width: 5px;
	height: 5px;
	margin: 5px 0 0 10px;
	border: 4px solid transparent;
	border-top: 5px solid #fff;
	box-sizing: border-box;
	transition: .1s;
}
.site_header_login details[open] summary:after {
	transform: rotate(180deg);
	margin: 0 0 5px 10px;
}
.site_header_login_menu {
	width: 165px;
	box-sizing: border-box;
	border: 1px solid #464646;
	background-color: #fff;
	position: absolute;
	top: 100%;
	right: 0;
}
.site_header_login_menu li:not(:last-child) {
	border-bottom: 1px solid #464646;
}
.site_header_login_menu li {}
.site_header_login_menu li a {
	box-sizing: border-box;
	display: flex;
	align-items: center;
	height: 50px;
	padding: 10px;
	color: #464646;
	text-decoration: none;
}
.site_header_login details[open] :last-child {
	animation: fadeIn 0.5s ease;
}
@keyframes fadeIn {
	0% {
		opacity: 0;
		transform: translateY(-10px);
	}
	100% {
		opacity: 1;
		transform: none;
	}
}
@media screen and (min-width: 801px) {}
@media screen and (min-width: 1200px) {}
@media screen and (max-width: 1199px) {}
@media screen and (max-width: 800px) {}
/* ======================================================
	sidebar
====================================================== */
.wrapper {
	width: 100%;
	display: flex;
}
.site_side {
	transition: all 1s;
	z-index: 3;
}
.site_main {
	transition: all 1s;
	flex-grow: 1;
}
#sidebarMenu {
	width: 0;
	height: 100%;
	position: relative;
	top: 0;
	left: 0;
	transform: translateX(-260px);
	transition: transform 250ms ease-in-out;
	background: #F2F0EE;
	color: #464646;
	font-size: 1.4rem;
}
.sidebarMenuInner {
	border-top: 1px solid rgba(255, 255, 255, 0.10);
}
.sidebarMenuInner > li {
	padding: 20px 30px;
	border-bottom: 2px solid #fff;
}
.sidebarMenuInner li a {
	color: #464646;
	text-decoration: none;
}
.sidebarMenuInner summary {
	display: block;
	cursor: pointer;
}
.sidebarMenuInner summary span {
	display: block;
	height: 20px;
	line-height: 20px;
	padding-left: 22px;
	background-size: 20px auto;
	background-position: 0 0;
	background-repeat: no-repeat;
	font-weight: bold;
}
.sidebarMenuInner summary .icn_01 {
	background-image: url(../img/common/icn_user.png);
}
.sidebarMenuInner summary .icn_02 {
	background-image: url(../img/common/icn_calendar.png);
}
.sidebarMenuInner summary .icn_03 {
	background-image: url(../img/common/icn_recruit.png);
}
.sidebarMenuInner summary .icn_04 {
	background-image: url(../img/common/icn_cash.png);
}
.sidebarMenuInner summary .icn_05 {
	background-image: url(../img/common/icn_hp.png);
}
.sidebarMenuInner summary .icn_06 {
	background-image: url(../img/common/icn_customer.png);
}
.sidebarMenuInner summary .icn_07 {
	background-image: url(../img/common/icn_monitoring.png);
}
.sidebarMenuInner summary .icn_08 {
	background-image: url(../img/common/icn_setting.png);
}
.site_sidemenu_inr {
	padding-left: 22px;
	margin-top: 10px;
}
.site_sidemenu_inr li:not(:last-child) {
	margin-bottom: 10px;
}
input[type="checkbox"]:checked ~ #sidebarMenu {
	transform: translateX(0);
	width: 260px;
}

input[type=checkbox] {
	transition: all 0.3s;
	box-sizing: border-box;
	display: none;
}
.sidebarIconToggle {
	transition: all 0.3s;
	box-sizing: border-box;
	cursor: pointer;
	position: absolute;
	z-index: 1;
	height: 100%;
	width: 100%;
	top: 65px;
	left: 0;
	height: 50px;
	width: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #F2F0EE;
	border-radius: 0 5px 5px 0;
}
input[type=checkbox]:checked ~ .sidebarIconToggle {
	left: 260px;
}
.spinner {
	transition: all 0.3s;
	width: 5px;
	height: 5px;
	border-width: 2px 2px 0 0;
	border-color: #464646;
	border-style: solid;
	transform: rotate(45deg);
}
input[type=checkbox]:checked ~ .sidebarIconToggle > .spinner {
	transform: rotate(225deg);
}
