/* ===========================
.btn_area
=========================== */
.btn_area {
	display: flex;
}
.btn_area * + * {
	margin-left: .5em;
}
.btn_style_01,
.btn_style_02,
.btn_style_03 {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 60px;
	height: 25px;
	box-sizing: border-box;
	border-radius: 5px;
	border: none;
	appearance: none;
	font-size: 1.4rem;
	font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}
.btn_style_01 {
	background-color: #5BC0DE;
	color: #fff;
}
.btn_style_02 {
	background-color: #F2F0EE;
	border: 1px solid #E0E1E3;
	color: #464646;
	&--02{
		width: 96px;
	}
}
.btn_style_03 {
	background-color: #F2F0EE;
	border: 1px solid #E0E1E3;
	color: #F04850;
	&--large{
		width: 150px;
	}
}
.btn_style_04 {
	box-sizing: border-box;
	cursor: pointer;
	display: flex;
	text-align: center;
	justify-content: center;
	align-items: center;
	min-width: 100px;
	padding: 10px;
	height: 40px;
	font-weight: bold;
	text-decoration: none;
	border-radius: 5px;
	border: none;
	background-color: #5BC0DE;
	color: #fff;
	appearance: none;
	font-size: 1.45rem;
	font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
	&--gray{
		background: #E0E1E3;
		color: #969696;
		margin-right: 18px;
	}
	&--border{
		border: 1px solid #969696;
		color: #969696;
		background: none;
		margin-right: 18px;
	}
}
.btn_style_05{
	position: relative;
	background: #fff;
	width: 225px;
	margin-bottom: 20px;
	margin-right: 30px;
	text-align: left;
	border: 1px solid #E0E1E3;
	padding: 7px 25px 7px 8px;
	border-radius: 4px;
	cursor: pointer;
	&:before{
		content: "";
		display: block;
		width: 31px;
		border-left: 1px solid #E0E1E3;
		border-radius: 0 3px 3px 0;
		background-color: #F2F0EE;
		position: absolute;
		top: 0px;
		right: 0px;
		bottom: 0px;
	}
	&:after{
		content: "";
		display: block;
		width: 15px;
		height: 15px;
		background: url(../img/common/icn_sort.png) 0 0 no-repeat;
		background-size: 100% auto;
		position: absolute;
		top: 50%;
		right: 9px;
		-webkit-transform: translateY(-50%);
		-ms-transform: translateY(-50%);
		transform: translateY(-50%);
	}
}
.btn_style_06{
	position: relative;
	margin-bottom: 20px;
	text-align: left;
	background: #fff;
	border: 1px solid #E0E1E3;
	padding: 7px 25px 7px 8px;
	border-radius: 4px;
	cursor: pointer;
	min-width: 120px;
	&:before{
		content: "";
		display: block;
		width: 30px;
		border-left: 1px solid #E0E1E3;
		border-radius: 0 3px 3px 0;
		background-color: #F2F0EE;
		position: absolute;
		top: 0px;
		right: 0px;
		bottom: 0px;
	}
	&:after{
		content: "";
		display: block;
		width: 5px;
		height: 5px;
		border-top: 2px solid #464646;
		border-right: 2px solid #464646;
		position: absolute;
		top: 47%;
		right: 13px;
		-webkit-transform: translateY(-50%) rotate(135deg);
		-ms-transform: translateY(-50%) rotate(135deg);
		transform: translateY(-50%) rotate(135deg);
	}
}
.btn_style_07{
	background-color: #F2F0EE;
	border: 1px solid #E0E1E3;
	color: #464646;
	height: 25px;
	@include font-size(14);
	border-radius: 5px;
	@include flex(center,center,wrap);
	&:hover{
		opacity: 0.7;
		cursor: pointer;
	}
}
/* ===========================
.statusicn
=========================== */
.statusicn {
	display: flex;
	width: 90px;
	height: 25px;
	justify-content: center;
	align-items: center;
	border-radius: 25px;
	box-sizing: border-box;
	border: 1px solid;
	text-align: center;
	font-size: 1.3rem;
	font-weight: bold;
	position: relative;
	&--02{
		width: 63px;
	}
	&--03{
		width: 72px;
	}
	&--04{
		width: 76px;
	}
}
.statusicn_completion {
	background-color: #464646;
	color: #fff;
}
.statusicn_supporting,
.statusicn_request,
.statusicn_quotation,
.statusicn_awaiting {
	background-color: #fff;
	color: #7DD8C9;
	border: 1px solid #7DD8C9;
}
.statusicn_awaitingreport,
.statusicn_awaitingapproval{
	background-color: #7DD8C9;
	color: #fff;
}
.statusicn_awaitingreport{
	font-size: 1.2rem;
}
.statusicn_unsupported {
	background-color: #fff;
	color: #DC5655;
	border: 1px solid #DC5655;
}
.statusicn_confirmation,
.statusicn_confirmation_ok,
.statusicn_confirmation_support{
	background-color: #fff;
	color: #F04850;
	border: 1px solid #F04850;
}
.statusicn_confirmation_open{
	background: #464646 !important;
	color: #fff !important;
	border-width: 0 !important;
	width: 63px !important;
}
.statusicn_expiration,
.statusicn_cancel {
	background-color: #fff;
	color: #969696;
	border: 1px solid #969696;
}
.statusicn_completion_off{
	background: #E0E1E3;
}
.statusicn::before {
	content: "";
	display: none;
	width: 15px;
	height: 15px;
	border: 2px solid #fff;
	border-radius: 100%;
	position: absolute;
	top: -4px;
	right: -10px;
}
.statusicn::after {
	content: "";
	display: none;
	width: 6px;
	height: 6px;
	border: 2.5px solid #fff;
	border-radius: 100%;
	position: absolute;
	top: 0px;
	right: -6px;
}
.statusicn_confirmation_ok::before,
.statusicn_confirmation_ok::after{
	display: block;
	background-color: #78BEDB;
}
.statusicn_confirmation_support::before,
.statusicn_confirmation_support::after{
	display: block;
	background-color: #F04850;
}
.statusicn_confirmation_error::before {
	content: "";
	display: block;
	width: 15px;
	height: 15px;
	border: 2px solid #fff;
	border-radius: 100%;
	position: absolute;
	top: -4px;
	right: -10px;
	background: #F04850;
}
.statusicn_confirmation_error::after {
	content: "";
	display: block;
	width: 9.5px;
	height: 3px;
	border: none;
	background: #fff;
	border-radius: 0;
	position: absolute;
	top: 3.5px;
	right: -5px;
}
