.icn_watching {
	display: block;
	width: 22px;
	height: 22px;
	line-height: 22px;
	border-radius: 22px;
	background-color: #464646;
	color: #fff;
	text-align: center;
	margin: 0 5px;
}
/* ===========================
form reset
=========================== */
input, select, textarea {
	font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
	box-sizing: border-box;
	width: 100%;
	background-color: #fff;
	padding: 15px;
	border: 0;
	border-radius: none;
	color: #464646;
	appearance: none;
	-webkit-appearance: none;
	box-shadow: none;
	outline: none;
	text-overflow: ellipsis;
	font-size: 1em;
	line-height: 1.2;
	border: 1px solid #E0E1E3;
}
select {
	border-radius: 5px;
}
input[type="radio"], input[type="checkbox"] {
	display: none;
}
select::-ms-expand {
	display: none;
}
::placeholder {
	color: #BAB8B8;
}
input:disabled {
	background: #F2F0EE;
}


