	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 9999;
	overflow-y: auto;
	overflow-x: hidden;
	opacity: 0;
	visibility: hidden;
	transition: all .3s ease;
	background: rgba(0,0,0,.6);
	@include flex(center,center,wrap);
	&.open{
		opacity: 1;
		visibility: visible;
		transition: all .3s ease;
	}
}

.popup-block{
	position: relative;
	background: #fff;
	width: 800px;
	border: 2px #78BEDB solid;
	&__close{
		position: absolute;
		top: 10px;
