//Styles input radio
.pure-input{
	input{
		border: 0;
		clip: rect(0 0 0 0);
		height: 1px;
		margin: -1px;
		overflow: hidden;
		padding: 0;
		position: absolute;
		width: 1px;
		&[type="radio"]{
			&:checked{
				& + label{
					&:before{
						animation: borderscale 300ms ease-in;
						background-color: #5BC0DE;
					}
					&:after{
						@include smooth-transition;
						transform: rotate(45deg) scale(1);
						opacity: 1;
					}
				}
			}
		}
		&[type="checkbox"]{
			&:checked{
				& + label{
					&:before{
						animation: borderscale 300ms ease-in;
						background-color: #5BC0DE;
					}
					&:after{
						@include smooth-transition;
						transform: rotate(45deg) scale(1);
						opacity: 1;
					}
				}
			}
		}
	}
	label, span{
		font-weight: 500;
		position: relative;
		padding: 0 0 0 2em;
		vertical-align: middle;
		user-select: none;
		cursor: pointer;
		&:before{
			box-sizing: content-box;
			content: '';
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 20px;
			height: 20px;
			//margin-top: -9px;
			border: 1px solid #E0E1E3;
			text-align: center;
			transition: all 0.4s ease;
			border-radius: 4px;
			background: #FAFAFA;
		}
		&:after{
			box-sizing: content-box;
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 7px;
			width: 5px;
			height: 10px;
			border: solid white;
			border-width: 0 3px 3px 0;
			transform: rotate(45deg) scale(0);
			opacity: 0;
			@include smooth-transition;
		}
	}


	&--styles02{
		label, span{
			padding-left: 3.2rem;
			&:before{
				width: 24px;
				height: 24px;
				border: 1px solid #E0E1E3;
				background: #FAFAFA;
				border-radius: 50%;
			}
			&:after{
				width: 16px;
				height: 16px;
				border-radius: 50%;
				background: rgba(#5BC0DE, 0.98);
				border: none;
				opacity: 0;
				transform: scale(0);
				@include smooth-transition;
			    top: 0;
				left: 5px;
			}
		}
		input[type=radio]:checked  + label{
			&:before{
				background-color: #fff;
			}
			&:after{
				opacity: 1;
				transform: scale(1);
				@include smooth-transition;
			}
		}
		input[type=radio]:checked  + span{
			&:before{
				background-color: #fff;
			}
			&:after{
				opacity: 1;
				transform: scale(1);
				@include smooth-transition;
			}
		}
	}
	&--styles03{
		border: 1px #E0E1E3 solid;
		border-right: none;
		border-radius: 4px 0 0 4px;
		width: 42px;
		height: 40px;
		@include flex(center,center,wrap);
		label{
			padding-left: 3.2rem;
			&:before{
				left: -5px;
				right: 0;
				width: 42px;
				height: 40px;
				background: #fff;
				border: none;
				border-radius: 4px 0 0 4px;
			}
			&:after{
				width: 12px;
				height: 12px;
				border-radius: 50%;
				border: 2px solid #969696;
				transform: scale(1);
				opacity: 1;
				@include v-h-align();
				z-index: 3;
			}
		}
		input[type=radio]:checked  + label{
			&:before{
				background: #5BC0DE;
			}
			&:after{
				border: 2px #fff solid;
				@include v-h-align();
			}
		}
	}
	&--styles04{
		border: 1px #E0E1E3 solid;
		border-radius: 0 4px 4px 0;
		width: 42px;
		height: 40px;
		@include flex(center,center,wrap);
		label{
			padding-left: 3.2rem;
			&:before{
				@include v-h-align();
				width: 16px;
				height: 16px;
				background: url("../img/common/icn_close02.png") no-repeat center/100%;
				border: none;
			}
			&:after{
				left: -5px;
				right: 0;
				top: -20px;
				width: 42px;
				height: 40px;
				background: #fff;
				border: none;
				border-radius: 0 4px 4px 0;
				transform: rotate(0deg) scale(1);
			}
		}
		input[type=radio]:checked  + label{
			&:before{
				background: url("../img/common/icn_close02-active.png") no-repeat center/100%;
				z-index: 2;
			}
			&:after{
				background: #5BC0DE;
				transform: rotate(0deg) scale(1);
			}
		}
	}
}
