/* ====================================================
Layout
==================================================== */
.user-heading{
	.col2_block_main{
		> .form_block{
			> .form_block{
				&:first-of-type{
					max-width: calc(100% - 176px);
					.form_part{
						&:first-of-type{
							//width: calc(100% - 320px);
							width: calc(70% - 20px);
						}
						&:last-of-type{
							margin-right: 0;
							width: 30%;
						}
					}
				}
			}
		}
		.form_part_btnarea{
			width: 136px;
		}
	}
	&--main{
		.col2_block_side_user_name{
			span{
				margin-top: 12px;
				display: block;
				@include font-size(14);
			}
		}
	}
}

.user-memo{
	> .form_block{
		width: calc(100% - 140px);
		.form_part{
			&:first-of-type{
				width: calc(70% - 20px);
			}
			&:last-of-type{
				margin-right: 0;
				width: 30%;
			}
		}
	}
}
.user-memo02{
	width: 100%;
	.form_part{
		&:first-of-type{
			width: calc(70% - 20px);
		}
		&:last-of-type{
			margin-right: 0;
			width: 30%;
		}
	}
}

.form_block__status{
	max-width: calc(100% - 135px);
	.form_part_com{
		width: 100%;
		@media screen and (min-width: 1860px) {
			margin-left: 205px;
		}
	}
}

.form_block__status02{
	max-width: calc(100% - 290px);
	.form_part_com{
		width: 100%;
		@media screen and (min-width: 1860px) {
			margin-left: 205px;
		}
	}
}
.form_part_editbtn{
	padding-left: 20px;
}

.error{
	border-radius: 4px;
	background: #FED3CF;
	color: #464646;
	font-weight: 500;
	margin-top: -4px;
	padding: 9px 15px;
	border: 1px solid #DC5655;
	@include flex(center,center);
	@include font-size(14);
	img{
		width: 20px;
		margin-right: 5px;
	}
}

